$color-syntax1:#7f7f7f;
$color-syntax2: #66d9ef;
$color-syntax3: #f92672;
$color-syntax4: #a6e22e;
$color-syntax5: #ae81ff;
$color-syntax6: #e6db74;
$color-syntax7: #75715e;

$color-syntax1: $color-dt-font-perceptive2;
$color-syntax2: #66d9ef;
$color-syntax3: $color-light-red;
$color-syntax4: $color-funky-green;
$color-syntax5: $color-lila;
$color-syntax6: #e6db74;
$color-syntax7: $color-dt-font-perceptive3;



/* Background */ .chroma { color: #f8f8f2; background-color: #272822 }
/* Error */ .chroma .err { color: #960050; background-color: #1e0010 }
/* LineTableTD */ .chroma .lntd { vertical-align: top; padding: 0; margin: 0; border: 0; }
/* LineTable */ .chroma .lntable { border-spacing: 0; padding: 0; margin: 0; border: 0; width: auto; overflow: auto; display: block; }
/* LineHighlight */ .chroma .hl { display: block; width: 100%;background-color: #ffffcc }
/* LineNumbersTable */ .chroma .lnt { margin-right: 0.4em; padding: 0 0.4em 0 0.4em;color: $color-syntax1 }
/* LineNumbers */ .chroma .ln { margin-right: 0.4em; padding: 0 0.4em 0 0.4em;color: $color-syntax1 }
/* Keyword */ .chroma .k { color: $color-syntax2 }
/* KeywordConstant */ .chroma .kc { color: $color-syntax2 }
/* KeywordDeclaration */ .chroma .kd { color: $color-syntax2 }
/* KeywordNamespace */ .chroma .kn { color: $color-syntax3 }
/* KeywordPseudo */ .chroma .kp { color: $color-syntax2 }
/* KeywordReserved */ .chroma .kr { color: $color-syntax2 }
/* KeywordType */ .chroma .kt { color: $color-syntax2 }
/* NameAttribute */ .chroma .na { color: $color-syntax4 }
/* NameClass */ .chroma .nc { color: $color-syntax4 }
/* NameConstant */ .chroma .no { color: $color-syntax2 }
/* NameDecorator */ .chroma .nd { color: $color-syntax4 }
/* NameException */ .chroma .ne { color: $color-syntax4 }
/* NameFunction */ .chroma .nf { color: $color-syntax4 }
/* NameOther */ .chroma .nx { color: $color-syntax4 }
/* NameTag */ .chroma .nt { color: $color-syntax3 }
/* Literal */ .chroma .l { color: $color-syntax5 }
/* LiteralDate */ .chroma .ld { color: $color-syntax6 }
/* LiteralString */ .chroma .s { color: $color-syntax6 }
/* LiteralStringAffix */ .chroma .sa { color: $color-syntax6 }
/* LiteralStringBacktick */ .chroma .sb { color: $color-syntax6 }
/* LiteralStringChar */ .chroma .sc { color: $color-syntax6 }
/* LiteralStringDelimiter */ .chroma .dl { color: $color-syntax6 }
/* LiteralStringDoc */ .chroma .sd { color: $color-syntax6 }
/* LiteralStringDouble */ .chroma .s2 { color: $color-syntax6 }
/* LiteralStringEscape */ .chroma .se { color: $color-syntax5 }
/* LiteralStringHeredoc */ .chroma .sh { color: $color-syntax6 }
/* LiteralStringInterpol */ .chroma .si { color: $color-syntax6 }
/* LiteralStringOther */ .chroma .sx { color: $color-syntax6 }
/* LiteralStringRegex */ .chroma .sr { color: $color-syntax6 }
/* LiteralStringSingle */ .chroma .s1 { color: $color-syntax6 }
/* LiteralStringSymbol */ .chroma .ss { color: $color-syntax6 }
/* LiteralNumber */ .chroma .m { color: $color-syntax5 }
/* LiteralNumberBin */ .chroma .mb { color: $color-syntax5 }
/* LiteralNumberFloat */ .chroma .mf { color: $color-syntax5 }
/* LiteralNumberHex */ .chroma .mh { color: $color-syntax5 }
/* LiteralNumberInteger */ .chroma .mi { color: $color-syntax5 }
/* LiteralNumberIntegerLong */ .chroma .il { color: $color-syntax5 }
/* LiteralNumberOct */ .chroma .mo { color: $color-syntax5 }
/* Operator */ .chroma .o { color: $color-syntax3 }
/* OperatorWord */ .chroma .ow { color: $color-syntax3 }
/* Comment */ .chroma .c { color: $color-syntax7 }
/* CommentHashbang */ .chroma .ch { color: $color-syntax7 }
/* CommentMultiline */ .chroma .cm { color: $color-syntax7 }
/* CommentSingle */ .chroma .c1 { color: $color-syntax7 }
/* CommentSpecial */ .chroma .cs { color: $color-syntax7 }
/* CommentPreproc */ .chroma .cp { color: $color-syntax7 }
/* CommentPreprocFile */ .chroma .cpf { color: $color-syntax7 }
/* GenericDeleted */ .chroma .gd { color: $color-syntax3 }
/* GenericEmph */ .chroma .ge { font-style: italic }
/* GenericInserted */ .chroma .gi { color: $color-syntax4 }
/* GenericStrong */ .chroma .gs { font-weight: bold }
/* GenericSubheading */ .chroma .gu { color: $color-syntax7 }
