/*
 * Signify that something is an Object, and that it may be used in any number of unrelated contexts to the one you can currently see it in.
 * Making modifications to these types of class could potentially have knock-on effects in a lot of other unrelated places.
 *
 * Tread carefully.
 */

.o-base-wrapper {
  display: flex;
  flex-direction: column;

  .o-base-wrapper__monsta {
    position: fixed;
    z-index: 99;

    @include breakpoint(xs) {
      max-width: 150px;
      transform: rotate(180deg);
      top: 0;
      right: 0; }

    @include breakpoint(sm) {
      max-width: 200px;
      transform: rotate(180deg);
      top: 0;
      right: 0; }

    @include breakpoint(md) {
      max-width: 250px; }

    @include breakpoint(lg) {
      bottom: -0%;
      left: -0%;
      top: initial;
      right: initial;
      transform: none;
      max-width: 20%; } }

  .o-base-wrapper__content {
    flex: 1 0 auto; }

  .o-base-wrapper__footer {
    flex-shrink: 0; } }

.o-article-wrapper {
  box-sizing: border-box;
  width: 100%;
  padding: 13% 8% 12% 8%;
  margin: auto;

  @include breakpoint(md) {
    box-sizing: content-box;
    width: 850px; }

  @include breakpoint(lg) {
    padding: 13% 5% 12% 15%; } }

.o-back-home {
  position: static;
  margin: 50px 0 0 50px;

  .o-back-home__caret {
    height: $font-size-heading1;
    color: $color-dt-font-heading; } }


.o-footer {
  .o-footer_list {
    list-style: none;
    float: right;
    padding: 0; }

  .o-footer_elem {
    float: left;
    margin-right: 2rem;

    &:last-of-type {
      margin-right: 2rem; } }

  .o-footer_link {
    color: $color-dt-font-perceptive2;
    transition: all 0.5s ease;
    text-decoration: none;
    font-size: $font-size-code;
    @include fontJetBrainsMono();
    text-transform: uppercase;

    &:hover {
      color: $color-dt-font-perceptive3; } } }


.o-post-list-item {
  margin-bottom: 10px;

  .o-post-list-item__strong {
    font-weight: bold;
    margin-right: 08px; }

  .o-post-list-item__date {
    @include fontJetBrainsMono();
    text-transform: uppercase;
    font-size: 0.9rem; } }

.o-h1 {
  color: $color-dt-font-heading;
  @include fontJetBrainsMono();
  font-size: $font-size-heading1;
  margin: 0 0 4rem 0;

  .o-h1__colored {
    @include gardientRedPurple;
    padding: 0 0.2rem;
    color: $color-lt-font-heading; }

  .o-h1__h1 {
    color: $color-dt-font-heading;
    @include fontJetBrainsMono();
    font-size: $font-size-heading1;
    margin: 0; }

  .o-h1__under {
    font-size: $font-size-code;
    text-transform: uppercase;
    color: $color-dt-font-perceptive1;
    margin-top: 0.5rem; } }


.o-h2 {
  color: $color-dt-font-heading;
  @include fontJetBrainsMono();
  font-size: $font-size-heading3;
  padding-bottom: 0.3rem;
  border-bottom: 0.1rem solid $color-white;
  margin: 4.0rem 0 1.5rem 0; }

.o-h3 {
  color: $color-dt-font-heading;
  @include fontJetBrainsMono();
  font-size: $font-size-heading3;
  margin: 4.0rem 0 1.5rem 0; }

.o-text {
  @include fontRoboto();
  color: $color-dt-font-perceptive1;
  font-size: $font-size-text;
  line-height: $line-height-text;
  margin: 0.5rem 0; }


.o-link {
  color: $color-dt-font-heading;
  background-color: $color-dark-red;
  text-decoration: none;
  padding: 0.1rem 0.2rem;
  transition: background-color 0.5s ease;

  &:hover {
    background-color: $color-darker-red; } }

.o-more-decent-link {
  text-decoration: none;
  color: $color-dt-font-heading;
  transition: all 0.5s ease;

  &:hover {
    color: $color-dt-font-perceptive2 !important; } }

.o-img {
  max-width: 100%;
  margin: 0.5rem 0; }

ol {
  counter-reset: olc;
  list-style: none;
  margin: 2rem 0;
  padding: 0 0 0 1.7em;

  li {
    color: $color-dt-font-perceptive1;
    font-size: $font-size-text;
    @include fontRoboto();
    line-height: $line-height-text;
    position: relative;

    &:before {
      content: counter(olc) ".";
      counter-increment: olc;
      color: $color-light-red;
      position: absolute;
      left: -1.3em; } } }

.o-list {
  list-style: none;
  margin: 2rem 0;
  padding: 0 0 0 1.7em;

  .o-list__item {
    position: relative;
    color: $color-dt-font-perceptive1;
    font-size: $font-size-text;
    @include fontRoboto();
    line-height: $line-height-text;

    &::before {
      content: "â€¢";
      color: $color-light-red;
      font-weight: bold;
      font-size: 2.5rem;
      line-height: 1;
      position: absolute;
      left: -0.7em;
      top: -0.1em; } } }

.o-code, .chroma {
  background-color: $color-dt-background2;
  padding: 1rem;
  font-size: $font-size-code;
  color: $color-dt-font-perceptive1;
  @include fontJetBrainsMono(); }

.o-bold {
  color: $color-dt-font-heading;
  font-weight: bold; }

.o-quote {
  background-color: $color-dt-background2;
  padding: 1rem;
  margin-right: 0;
  margin-left: 0;
  display: flex;
  flex-wrap: wrap;

  .o-quote__quote-container {
    display: flex;
    width: 100%; }

  .o-quote__mark {
    width: 5%;
    color: $color-dt-font-heading;
    font-size: 4rem;
    @include fontRoboto; }

  .o-quote__quote {
    @include fontRoboto;
    line-height: $line-height-text;
    font-size: $font-size-text;
    color: $color-dt-font-perceptive1;
    width: 90%;

    @media (max-width: 600px) {
      padding: 12% 0 0 0; }

    @media (min-width: 601px) {
      padding: 6% 0 3% 0; }

    @include breakpoint(md) {
      padding: 5% 0 2% 0; } }

  .o-quote__from {
    @include fontJetBrainsMono;
    text-align: right;
    width: 100%;
    font-size: $font-size-text;
    color: $color-dt-font-perceptive1;

    &::before {
      content: "\2014  ";
      @include fontJetBrainsMono; } } }
