
$font-size-code: 1rem;
$font-size-text: 1.3rem;
$font-size-heading1: 2.8rem;
$font-size-heading2: 1.45rem;
$font-size-heading3: $font-size-heading2;

$line-height-text: 2rem;

@mixin breakpoint($class) {
  @if $class == xs {
    @media (max-width: 767px) {
      @content; } }
  @else if $class == sm {
    @media (min-width: 768px) {
      @content; } }
  @else if $class == md {
    @media (min-width: 992px) {
      @content; } }
  @else if $class == lg {
    @media (min-width: 1300px) {
      @content; } }
  @else {
    @warn "Breakpoint mixin supports: xs, sm, md, lg"; } }

