$color-black: #000;
$color-black2: #1C1C1C;
$color-black3: #2C2C2C;

$color-white: #fff;
$color-white2: #E7E7E7;

$color-light-red: #E20E5A;
$color-dark-red: #BC0B4B;
$color-darker-red: #71072D;

$color-purple: #5c164e; // for color gradient

$color-funky-green: #A6E22E;
$color-twitter-blue: #1DA1F2;
$color-lila: #CF6BF7;


// Dark Theme
$color-dt-font-perceptive3: #676767;
$color-dt-font-perceptive2: #A7A7A7;
$color-dt-font-perceptive1: $color-white2;
$color-dt-font-heading: $color-white;
$color-dt-background1: $color-black2;
$color-dt-background2: $color-black3;

// Light Theme
$color-lt-font-heading: $color-black;

@mixin gardientRedPurple() {
  background-color: #bc0b4b;
  background-image: linear-gradient(45deg, $color-dark-red 0%, $color-purple 100%);
}